.header__button-wrap {
  display: flex;
  flex-wrap: wrap;

  @media screen and (max-width: 767.98px) {
    justify-content: space-between;
  }

  @media screen and (min-width: 768px) {
    justify-content: center;
  }
}

.button__primary-light {
  @extend %button;
  @extend %button__primary;
  @include btn-color($button-color, $text-color-secondary);
  @include border($border-color-light);

  &:hover,
  &:focus {
    background-color: $button-color-accent-hover;
    border: 0;
  }
}

.button__primary-accent {
  @extend %button;
  @extend %button__primary;
  @include btn-color($button-color-accent, $text-color-secondary);
  border: 0;

  &:hover,
  &:focus {
    background-color: $button-color-accent-hover;
  }
}

.header__button:not(:last-child) {
  @media screen and (min-width: 768px) {
    margin-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 16px;
  }
}

