.card__button-wrap {
  display: flex;
  flex-wrap: wrap;

  @media screen and (max-width: 767.98px) {
    justify-content: space-between;
  }
}

.button__secondary {
  @extend %button;
  @include btn-color($button-color, $text-color-primary);
  @include border($border-color-dark);

  min-width: 110px;
  height: 44px;
  text-transform: uppercase;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  cursor: pointer;

  &:hover,
  &:focus {
    border: 0;
    color: $text-color-secondary;
    background-color: $button-color-accent;
  }

  @media screen and (min-width: 768px) {
    min-width: 125px;
  }

  @media screen and (min-width: 1280px) {
    min-width: 136px;
  }
}

.card__button:not(:last-child) {
  @media screen and (min-width: 768px) {
    margin-right: 15px;
  }
}

.button__load-more {
  @extend %button;
  @extend %button__primary;
  @include btn-color($button-color-accent, $text-color-secondary);
  border: 0;
  margin-left: auto;
  margin-right: auto;

  &:hover,
  &:focus {
    background-color: $button-color-accent-hover;
  }

}

.card__button:not(:last-child) {
  @media screen and (min-width: 768px) {
    margin-right: 15px;
  }
}

.show {
  display: block;
}

.container__load-more {
  display: flex;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 20px;
}

.disabled-btn {
  @extend %button;
  @include btn-color($button-color, $text-color-primary);
  @include border($border-color-dark);

  min-width: 110px;
  height: 44px;
  text-transform: uppercase;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}